.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 414px) {
  .container {
    width: 414px;
  }
}

@media (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.button {
  color: #f3c653;
  letter-spacing: .02em;
  background-color: #000;
  border: 2px solid #fff;
  border-radius: 25px;
  padding: 14px 35px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button:hover, .button:focus {
  background-color: #fff;
  border-color: #f3c653;
}

.button-modal {
  height: 64px;
  color: #fff;
  background: #000;
  border: 1px solid #f3c653;
  border-radius: 2px;
  font-size: 24px;
  font-weight: 500;
}

.button-modal:hover, .button-modal:focus {
  color: #000;
  background: #f3c653;
}

.button-select {
  fill: #c79002;
  cursor: pointer;
  pointer-events: none;
  background-color: #fffdee;
  border: none;
  outline: none;
  margin-left: auto;
  padding: 0;
  display: block;
  position: absolute;
  top: 15%;
  right: 5px;
}

.secondary-button {
  letter-spacing: .02em;
  color: #f3c653;
  background: #000;
  border: 2px solid #fffdee;
  border-radius: 25px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.media-list {
  flex-direction: row;
  gap: 9px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.media-list__item {
  height: 24px;
  width: 24px;
}

.media-list__icon {
  fill: rgba(243, 198, 83, .7);
}

.media-list__icon:hover, .media-list__icon:focus {
  fill: #fff;
}

.address__control {
  flex-direction: row-reverse;
  display: flex;
}

.address__element {
  border: 1px solid rgba(243, 198, 83, .7);
  border-radius: 2px;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  padding: 8px 8px 8px 6px;
  display: inline-flex;
}

@media (min-width: 1024px) {
  .address__element {
    padding: 8px 8px 8px 10px;
  }
}

.address__link {
  letter-spacing: .01em;
  color: #e5e5e5;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  line-height: 1.25;
}

@media (min-width: 1024px) {
  .address__link {
    font-size: 16px;
    line-height: 1.25;
  }
}

.address__icon {
  fill: #f3c653;
}

